@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
body{
    margin: 0;
}

.main-container{
    display: flex;
    flex-direction: column;
}

.header{
    display: flex;
    align-items: center;
    position: fixed;
    background-color: #F0F1F6;
    width: 100%;
    z-index: 30;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.logo{
    width: 150px;
    padding: 20px;
}

.header-menu{
    width: 40%;
}

.header-menu-item{
    box-sizing: border-box;
    font-size: 18px;
    padding: 7px 30px;
    font-weight: 600;
    color: #242E41;
    cursor: pointer;
    font-family: 'Raleway', sans-serif;
}

.header-menu-item:hover{
    color: white;
    background-color: #242E41;
    border-radius: 15px;
}

.search-form{
    width: 25%;
    margin: auto;
}

.search-input{
    width: calc(90% - 24px);
    height: 10px;
    padding: 12px;
    background-color: transparent;
    border-radius: 20px;
    border: 1px solid #ccc;
    outline: none;
    font-family: 'Raleway', sans-serif;
}

.btn{
    width: 12%;
    margin: 0 auto;
    padding: 8px;
    border-radius: 10px;
    background-color: #242E41;
    color: white;
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    font-size: 16px;
    cursor: pointer;
}

.btn:hover{
    background-color: #f0f1f6;
    color: rgb(36,46,65);
    border: 1px solid rgb(36,46,65);
}

.body-container{
    display: flex;
    background-color: #F0F1F6;
}

.sidebar{
    display: flex;
    flex-direction: column;
    width: 20%;
    min-height: 100vh;
    padding-top: 10%;
    background-color: #242E41;
    position: fixed;
    z-index: 20;
}

.baller{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.team-name{
    width: 100%;
    height: 50px;
    background-color: transparent;
    outline: none;
    color: white;
    border: none;
    cursor: pointer;
    font-family: 'Raleway', sans-serif;
    font-size: 16px;
}

.team-name:first-child{
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    font-size: 20px;
}

.team-name:hover{
    background-color: lightgray;
    color: #242E41;
    font-weight: 800;
}

.active-team{
    width: 100%;
    height: 50px;
    background-color: lightgray;
    outline: none;
    color: #242E41;
    border: none;
    cursor: pointer;
    font-family: 'Raleway', sans-serif;
    font-size: 16px;
    font-weight: 800;
}

.player-container{
    width: 80%;
    display: flex;
    padding-top: 10%;
    flex-wrap: wrap;
    margin-left: 20%;
    min-height: 100vh;
}

.player-card{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    width: 25%;
    margin-left: 6%;
    margin-bottom: 3%;
    border-radius: 10px;
    padding: 5px;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}

.player-card img{
    width: 100%;
    height: 65%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.player-card p{
    font-weight: 600;
    margin: 10px;
}

.player-card h2{
    margin: 5px;
    font-size: 24px;
}

.player-card:hover{
    cursor: pointer;
    -webkit-transform: scale(1.05,1.05);
            transform: scale(1.05,1.05);
}
*, *:after, *:before {
	 box-sizing: border-box;
}
 body {
	 font-family: "DM Sans", sans-serif;
	 line-height: 1.5;
}
 img {
	 max-width: 100%;
	 display: block;
}
 input {
	 -webkit-appearance: none;
	         appearance: none;
	 border-radius: 0;
}
 .card {
	 margin: 2rem auto;
	 display: flex;
	 flex-direction: column;
	 width: 100%;
	 max-width: 425px;
	 background-color: #fff;
	 border-radius: 10px;
	 box-shadow: 0 10px 20px 0 rgba(153, 153, 153, .25);
	 padding: 0.75rem;
}
 .card-image {
	 border-radius: 8px;
	 overflow: hidden;
	 padding-bottom: 65%;
	 background-image: url('https://cdn.icccricketschedule.com/wp-content/uploads/2020/11/IPL-LogoHere-is-the-list-of-3-Players-from-each-team-who-might-get-retained-if-Mega-Auctions-happen-1024x683.jpg');
	 background-repeat: no-repeat;
	 background-size: cover;
	 background-position: 0 5%;
	 position: relative;
}
 .card-heading {
	 position: absolute;
	 left: 10%;
	 top: 15%;
	 right: 10%;
	 font-size: 1.75rem;
	 font-weight: 700;
	 color: #735400;
	 line-height: 1.222;
}
 .card-heading small {
	 display: block;
	 font-size: 0.75em;
	 font-weight: 400;
	 margin-top: 0.25em;
}
 .card-form {
	 padding: 2rem 1rem 0;
}
 .input {
	 display: flex;
	 flex-direction: column-reverse;
	 position: relative;
	 padding-top: 1.5rem;
}
 .input + .input {
	 margin-top: 1.5rem;
}
 .input-label {
	 color: #8597a3;
	 position: absolute;
	 top: 1.5rem;
	 transition: 0.25s ease;
}
 .input-field {
	 border: 0;
	 z-index: 1;
	 background-color: transparent;
	 border-bottom: 2px solid #eee;
	 font: inherit;
	 font-size: 1.125rem;
	 padding: 0.25rem 0;
}
 .input-field:focus, .input-field:valid {
	 outline: 0;
	 border-bottom-color: #6658d3;
}
 .input-field:focus + .input-label, .input-field:valid + .input-label {
	 color: #6658d3;
	 -webkit-transform: translateY(-1.5rem);
	         transform: translateY(-1.5rem);
}
 .action {
	 margin-top: 2rem;
}
 .action-button {
	 font: inherit;
	 font-size: 1.25rem;
	 padding: 1em;
	 width: 100%;
	 font-weight: 500;
	 background-color: #6658d3;
	 border-radius: 6px;
	 color: #fff;
	 border: 0;
	 cursor: pointer;
}
 .action-button:focus {
	 outline: 0;
}
 .card-info {
	 padding: 1rem 1rem;
	 text-align: center;
	 font-size: 0.875rem;
	 color: #8597a3;
}
 .card-info a {
	 display: block;
	 color: #6658d3;
	 text-decoration: none;
}
 
